import axios from 'axios'

import type { Club, ClubId } from 'src/models/Club'
import type { Recruit } from 'src/models/Recruit'

interface GetParameters {
  clubId: ClubId
}

// TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
export const getClubInformation = async ({ clubId }: GetParameters) => {
  const response = await axios.get<Club>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/`)
  return response.data
}

// TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
export const getClubRecruitInformation = async ({ clubId }: GetParameters) => {
  const response = await axios.get<Recruit>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/`)
  return response.data
}
