import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import InstagramIcon from '@mui/icons-material/Instagram'
import { Box, Button, Card, Chip, Container, Grid, Typography, useMediaQuery, useTheme } from '@mui/material'

import { CLUB_INFORMATION_MESSAGE, CLUB_RECRUITMENT_MESSAGE } from 'src/data/messages'
import { CLUB_TYPES } from 'src/data/types'

import { getClubInformation, getClubRecruitInformation } from 'src/api/club'

import type { Club } from 'src/models/Club'
import type { Recruit } from 'src/models/Recruit'

import PageTitleWrapper from 'src/components/PageTitleWrapper'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

const Intro: React.FC = () => {
  const { id } = useParams()
  const clubId = Number(id)
  const navigate = useNavigate()

  const theme = useTheme()
  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))

  const [club, setClub] = useState<Club | null>(null)
  const [recruitInfo, setRecruitInfo] = useState<Recruit>()

  const { userInfo } = useSafeAuthUser()
  const { enqueueSnackbar } = useSnackbar()

  const today = new Date()
  const startAt = recruitInfo ? new Date(recruitInfo.startAt) : today
  const endAt = recruitInfo ? new Date(recruitInfo.endAt) : today

  const isRecruitingNow = !!recruitInfo && startAt <= today && today <= endAt

  const userClub = userInfo?.clubs.find(c => c.id === clubId)
  const isMember = !!userClub
  const isConnected = isMember && userClub?.isConnected

  const getClubStates = async () => {
    try {
      const club = await getClubInformation({ clubId })
      setClub(club)
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? CLUB_INFORMATION_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
        return
      }
      enqueueSnackbar(CLUB_INFORMATION_MESSAGE.UNKNOWN_ERROR, {
        variant: 'error'
      })
    }

    try {
      const recruitInfo = await getClubRecruitInformation({ clubId })
      setRecruitInfo(recruitInfo)
    } catch (err) {
      if (err instanceof AxiosError) {
        // 동아리에서 모집하고 있지 않다는 이유로 빨간 에러가 뜨면 마음이 아파서
        enqueueSnackbar(CLUB_RECRUITMENT_MESSAGE.NO_RECRUIT_MESSAGE, {
          variant: 'warning'
        })
        return
      }
      enqueueSnackbar(CLUB_RECRUITMENT_MESSAGE.UNKNOWN_ERROR, {
        variant: 'error'
      })
    }
  }

  useEffect(() => {
    getClubStates()
  }, [])

  return (
    <>
      <Helmet>
        <title>프로필</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container>
          {club && (
            <Grid item xs={12} md={9}>
              <Box display="flex" flexDirection="row" alignItems="center">
                <Typography variant="h3">{club.name}</Typography>
                {club?.instagramId && <InstagramConnect instagramId={club.instagramId} />}
              </Box>
              <Typography variant="subtitle2" gutterBottom>
                {club.description}
              </Typography>
              <Box>
                {CLUB_TYPES[club.type] && (
                  <Chip
                    label={CLUB_TYPES[club.type].name}
                    size="small"
                    sx={{ padding: theme.spacing(0.5), fontSize: isUpperThanSm ? 13 : 11 }}
                  />
                )}
                {club.category && (
                  <Chip
                    label={club.category}
                    size="small"
                    sx={{ padding: theme.spacing(0.5), fontSize: isUpperThanSm ? 13 : 11, marginLeft: 1 }}
                  />
                )}
                {club.location && (
                  <Chip
                    label={club.location}
                    size="small"
                    sx={{ padding: theme.spacing(0.5), fontSize: isUpperThanSm ? 13 : 11, marginLeft: 1 }}
                  />
                )}
              </Box>
            </Grid>
          )}
          <Grid item xs={12} md={3} mt={1} display="flex" flexDirection="column" justifyContent="center">
            <Box>
              <Typography variant="h4">
                모집 시작 <Typography display="inline">{recruitInfo ? startAt.toLocaleDateString() : '-'}</Typography>
              </Typography>
            </Box>
            <Box>
              <Typography variant="h4" display="inline">
                모집 종료 <Typography display="inline">{recruitInfo ? endAt.toLocaleDateString() : '-'}</Typography>
              </Typography>
            </Box>
            <Box>
              <Typography variant="h4" display="inline">
                정원 <Typography display="inline">{recruitInfo ? `${recruitInfo.limit}명` : '-'}</Typography>
              </Typography>
            </Box>
            {isMember ? (
              <Button
                href={`/club/${clubId}/profile`}
                variant="contained"
                sx={{ padding: theme.spacing(0.5), marginY: 1 }}
              >
                내 활동
              </Button>
            ) : (
              <Chip
                label={isRecruitingNow ? '모집 중' : '모집 기간이 아니에요'}
                sx={{ padding: theme.spacing(0.5), marginY: 1 }}
                color={isRecruitingNow ? 'success' : 'warning'}
              />
            )}
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="md">
        {recruitInfo && recruitInfo.contents ? (
          <Card>
            <Box p={2} overflow="auto">
              <div className="view ql-editor" dangerouslySetInnerHTML={{ __html: recruitInfo.contents }}></div>
            </Box>
          </Card>
        ) : (
          <Card>
            <Box height="50vh" display="flex" justifyContent="center" alignItems="center">
              <Typography variant="h4">동아리에서 올린 소개글이 없어요 ㅠ.ㅠ</Typography>
            </Box>
          </Card>
        )}
        <Box textAlign="center" my={2}>
          {isMember ? (
            <Button
              variant="contained"
              disabled={isConnected}
              sx={{ paddingX: 5 }}
              onClick={() => navigate('agreement')}
            >
              내 정보 제출하기
            </Button>
          ) : (
            <Button
              variant="contained"
              disabled={!isRecruitingNow || isMember}
              sx={{ paddingX: 5 }}
              onClick={() => navigate('apply')}
            >
              지원하기
            </Button>
          )}
        </Box>
      </Container>
    </>
  )
}

export default Intro

const InstagramConnect: React.FC<{ instagramId: string }> = ({ instagramId }) => {
  const handleClickInstagram = () => window.open(`https://instagram.com/${instagramId}`, '_blank')

  return (
    <Button onClick={handleClickInstagram}>
      <InstagramIconColored />
    </Button>
  )
}

const InstagramIconColored = () => (
  <>
    <svg width={0} height={0}>
      <linearGradient id="linearColors" x1={1} y1={0} x2={1} y2={1}>
        <stop offset={0} stopColor="#4f5bd5" />
        <stop offset={0.1} stopColor="#962fbf" />
        <stop offset={0.4} stopColor="#d62976" />
        <stop offset={0.6} stopColor="#fa7e1e" />
        <stop offset={0.9} stopColor="#feda75" />
      </linearGradient>
    </svg>
    <InstagramIcon sx={{ fill: 'url(#linearColors)' }} />
  </>
)
